@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

html, body, #root {
    height: 100%;
    margin: 0;
    background-color: black;
    color: #00ff88;
    font-family: 'Press Start 2P', monospace;
    scroll-behavior: smooth;
}

.App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    overflow-x: hidden;
    background-color: black;
}

/* CRT screen effect */
.crt-screen {
    position: relative;
    background-color: black;
    color: #00ff88;
    font-family: 'Press Start 2P', monospace;
    overflow: hidden;
    animation: flicker 1s infinite;
}

/* Header */
.header {
    background-color: black;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    border-bottom: 2px solid #00ff88;
}

    .header h1 {
        margin: 0;
        font-size: 1.2rem;
    }

    .header nav a {
        color: #00ff88;
        margin-left: 1rem;
        text-decoration: none;
    }

        .header nav a:hover {
            text-decoration: underline;
        }

/* Hero Section */
.hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 4rem 1rem 2rem;
    text-align: center;
    background-color: black;
    color: #00ff88;
}

/* About Section */
.about {
    padding: 4rem 2rem;
    background-color: black;
    color: #00ff88;
    text-align: center;
}

/* Projects Section */
.projects {
    padding: 4rem 2rem;
    background-color: black;
    color: #00ff88;
    text-align: center;
}

.project-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
    margin-top: 2rem;
}

.project-card {
    background-color: #111;
    border: 2px solid #00ff88;
    padding: 1rem;
    border-radius: 8px;
    transition: transform 0.3s ease;
}

    .project-card:hover {
        transform: scale(1.03);
    }

    .project-card img {
        width: 100%;
        height: auto;
        border-bottom: 1px solid #00ff88;
        margin-bottom: 1rem;
    }

/* Contact Section */
.contact {
    padding: 4rem 2rem;
    background-color: black;
    color: #00ff88;
    text-align: center;
}

/* Footer */
.footer {
    margin-top: auto;
    padding: 1rem;
    background-color: black;
    color: #00ff88;
    text-align: center;
}

/* Pixel font */
.pixel-font {
    font-family: 'Press Start 2P', monospace;
}

/* Loading Screen */
.loading-screen {
    background-color: black;
    color: #00ff88;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    position: fixed;
    width: 100%;
    height: 100%;
    inset: 0;
    z-index: 9999;
}

.progress-bar {
    width: 200px;
    height: 10px;
    background-color: #333;
    border: 1px solid #00ff88;
    margin-top: 1rem;
}

.bar-fill {
    width: 100%;
    height: 100%;
    background-color: #00ff88;
    animation: loading-fill 2s ease-in-out infinite;
}

/* Animations */
@keyframes flicker {
    0% {
        opacity: 1;
    }

    50% {
        opacity: 0.98;
    }

    100% {
        opacity: 1;
    }
}

@keyframes loading-fill {
    0% {
        width: 0%;
    }

    100% {
        width: 100%;
    }
}
